.loading {
    font-family: 'Russo One', sans-serif;
    width: 100%; height: 100%;
}
.loading text {
    text-transform: uppercase;
    animation: stroke 5s infinite alternate;
    stroke-width: 2;
    stroke: #365fa0;
    font-size: 140px;
    font-weight: 700;
}
@keyframes stroke {
    0%   {
        fill: rgba(72,138,20,0); stroke: var(--color-pink);
        stroke-dashoffset: 25%; stroke-dasharray: 0 50%; stroke-width: 2;
    }
    70%  {fill: rgba(72,138,20,0); stroke: var(--color-pink); }
    80%  {fill: rgba(72,138,20,0); stroke: var(--color-pink); stroke-width: 3; }
    100% {
        fill: var(--color-pink); stroke: rgba(54,95,160,0); 
        stroke-dashoffset: -25%; stroke-dasharray: 50% 0; stroke-width: 0;
    }
}